/* src/App.css */
body {
    margin: 0;
    font-family: Arial, sans-serif;
  }
  
  .App {
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #f0f0f0;
  }
  
  .header-container {
    display: flex;
    justify-content: center;
    padding: 20px;
    background-color: #f0f0f0;
    
  }
  
  .header {
    display: flex;
    align-items: center;
    padding-left: 150px;
  }
  
  .button-wrapper {
    display: flex;
    align-items: center;
    gap: 1000px; 
    background-color: white;
    border-radius: 50px;
    padding: 20px;
    box-shadow: inset 10px 10px 5px rgba(175, 175, 175, 0.5);
  }
  
  .button-icon {
    background-color: white;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .header-icon {
    width: 40px;
    height: 40px;
    cursor: pointer;
  }
  
  .content {
    display: flex;
    flex: 1;
  }
  
  .sidebar {
    width: 200px;
    background-color: #f0f0f0;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 0;
    
  }
  
  .sidebar nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .sidebar nav ul li {
    margin: 50px 0;
  }
  .tabIcon-wrapper {
    display: flex;
    align-items: center;
    background-color: white;
    border-radius: 15px;
    padding: 15px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5),-5px -5px 5px rgba(175, 175, 175, 0.5);
    
  }
  .tabIcon-wrapper.active {
    box-shadow: inset 10px 10px 5px rgba(175, 175, 175, 0.5);
  }
  .icon {
    width: 40px;
    height: 40px;
    cursor: pointer;
  }
  
  .main-content {
    width: 80%;
    height: 90%;
    padding: 20px;
    justify-content: center;
    align-items: center;
    background-color: #f0f0f0;
    border-radius: 20px;
    box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.5),-10px -10px 5px rgba(175, 175, 175, 0.5);
  }
  
  .main-content img {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }